.main-nav__list {
  margin: 0;
  padding: 0;
}

.main-nav__list:last-child {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  top: 22px;
  right: 60px;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .main-header__toggle {
    padding: 22px;
  }
  .main-nav--closed .main-header__toggle svg {
    display: block;
  }
  .main-nav--closed .main-header__toggle::after,
.main-nav--closed .main-header__toggle::before {
    display: none;
  }

  .main-nav--nojs {
    display: flex;
    flex-direction: column;
  }
  .main-nav--nojs .main-header__toggle + .main-nav__list {
    display: block;
    position: inherit;
    padding: 0;
    margin: 0;
    list-style-type: none;
    width: 320px;
    right: 0;
    top: 60px;
    text-align: center;
    z-index: 2;
    order: 1;
  }
  .main-nav--nojs .main-nav__list ~ .main-nav__list {
    position: inherit;
    order: 0;
    padding-left: 190px;
  }
  .main-nav--nojs .main-container {
    margin-top: 228px;
  }
  .main-nav--nojs .main-header__toggle {
    display: none;
  }
  .main-nav--nojs .main-header {
    padding-bottom: 290px;
  }

  .main-nav__list:last-child {
    width: 200px;
    justify-content: flex-end;
  }
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    align-items: center;
    padding-top: 5px;
  }
  .main-nav .main-nav__list {
    padding-right: 60px;
    align-items: baseline;
    flex-wrap: wrap;
  }

  .main-nav__list {
    display: flex;
  }
  .main-nav__list:last-child {
    padding-right: 0;
    position: inherit;
  }
}
@media (min-width: 1440px) {
