    justify-content: space-between;
    width: 882px;
    padding-top: 0;
  }

  .main-header__toggle + .main-nav__list {
    width: 460px;
    padding-right: 0;
    justify-content: space-between;
  }

  .main-nav__list ~ .main-nav__list {
